import { mapState } from 'vuex';
export default{
	data(){
		return{
			activeTab:0,
			status:"",
			page:1,
			size:10,
			hasMore:true,
			tabs:["全部","预定中","待支付","已支付","已发货","已取消"],
			data_list:[]
		}
	},
	computed:{
		...mapState(['config'])
	},
	methods:{
		async pay(orderId){
			 var that = this
			await this.$http({
				apiName:"wxPay",
				method:"POST",
				data:{orderId:orderId}
			}).then(res => {
				let obj = {
					nonceStr: res.nonceStr,
					timeStamp: res.timeStamp,
					package:res.packageValue,
					signType:res.signType,
					paySign:res.paySign,
					appId: res.appId,
				}
				uni.requestPayment({
				    provider: 'wxpay',
				    ...obj,
				    success: function (res) {
						uni.showToast({
							title: "支付成功",
							duration: 1500,
						})
						setTimeout(()=>{
							that.changeTab(3)
						},1500)
				    },
				    fail: function (err) {
				       uni.showToast({
							title: "支付失败",
							duration: 1500,
							icon:"none"
				       })
				    }
				});
			}).catch(_ => {})
			
		},
		changeTab(tab){
			if(this.activeTab == tab){
				return
			}else{
				this.activeTab = tab;
				this.hasMore = true;
				this.data_list = [];
				this.page = 1;
				if(tab == 0){this.status = ""}
				if(tab == 1){this.status = 0}
				if(tab == 2){this.status = 1}
				if(tab == 3){this.status = 2}
				if(tab == 4){this.status = 3}
				if(tab == 5){this.status = -1}
				this.getList()
			}
		},
		async getList(){
			let res = await this.$http({
				apiName:"orderList",
				data:{
					page:this.page,
					size:this.size,
					status:this.status
				}
			})
			try{
				this.data_list = this.data_list.concat(res.list);
				this.hasMore = res.hasNextPage
			}catch(e){}
		},
		getMore(){
			this.page ++;
			this.getList()
		},
		async cancelOrder(orderId){
			 let _self = this;
			 uni.showModal({
				 title: '提示',
				 content: '确定取消该订单吗',
				 success: function (res) {
					 if (res.confirm) {
						 _self.cancelOrderOk(orderId)
					 } else if (res.cancel) {
						 
					 }
				 }
			 });
						 
		},
		async cancelOrderOk(orderId){
			let _self = this;
			await this.$http({
				 apiName:"cancelOrder",
				 method:"POST",
				 data:{orderId:orderId}
			}).then(()=>{
				uni.showToast({
					title: "订单已取消",
					duration: 1500
				})
				setTimeout(_=>{
					_self.hasMore = true;
					_self.data_list = [];
					_self.page = 1;
					_self.getList()
				},1500)
			}).catch(e=>{console.log()})
		},
		copy(data){
			let _self = this
			uni.setClipboardData({
				data: data,
				success() {
					uni.showToast({
						title: "复制成功",
					})
				}
			});
		},
		toPath(path){
			uni.navigateTo({
				url:path
			})
		},
	},
	async onPullDownRefresh() {
		uni.showLoading({
			title:"数据加载中..."
		})
		this.hasMore = true;
		this.data_list = [];
		this.page = 1;
		await this.getList();
		uni.stopPullDownRefresh();
		uni.hideLoading();
	},
	async onShow() {
		this.hasMore = true;
		this.data_list = [];
		this.page = 1;
		uni.showLoading({
			title:"数据加载中..."
		})
		await this.getList();
		uni.hideLoading();
	}
}
